import React, { useEffect, useRef } from 'react';

// Images
import img from "../../assets/Images/landing-three-services-bg-e45e1b055f311202a329bb120b7c1975.jpg";
import landing_work_gallery from "../../assets/Images/landing-work-gallery-001-b71223b0a68b9bb8aa67044b6ff35e7a.jpg";
import landing_work_gallery022 from "../../assets/Images/landing-work-gallery-002-3ba2c575d9a440aaef36056b56d2dc40.jpg";
import landing_work_gallery027 from "../../assets/Images/landing-work-gallery-003-23ca19819e672e06ac7ff5683935f7d3.jpg";
import landing_work_gallery028 from "../../assets/Images/landing-work-gallery-004-aaeaadf6a9b68a656ad44ce908a44805.jpg";
import landing_work_gallery023 from "../../assets/Images/landing-work-gallery-005-1244192c49dbdd269554490a89cce498.jpg";
import landing_work_gallery024 from "../../assets/Images/landing-work-gallery-006-5d197dd29d97f308f70cfddcb9cd418d.jpg";
import landing_work_gallery025 from "../../assets/Images/landing-work-gallery-007-8309937dc3b3abeaae06c4f5f650bcc8.jpg";
import landing_work_gallery026 from "../../assets/Images/landing-work-gallery-003-23ca19819e672e06ac7ff5683935f7d3.jpg";

// Styles
import {
  our_work_container,
  photo_grid_container,
  photo_grid,
  photo_item,
  photo_image,
  photo_overlay,
  photo_category,
  overlay_content,
  work_header,
  work_subtitle,
  work_title
} from "../../Styles/work.module.css";

// Photo data
const photos = [
  { url: img, category: "Mango Fashion Lookbook Campaign", span: "row_span_1", title: "Designer Linocut" },
  { url: landing_work_gallery, category: "Mango Fashion Lookbook Campaign", span: "row_span_2", title: "Designer Linocut" },
  { url: landing_work_gallery022, category: "Mango Fashion Lookbook Campaign", span: "row_span_1", title: "Designer Linocut" },
  { url: landing_work_gallery023, category: "Mango Fashion Lookbook Campaign", span: "row_span_1", title: "Designer Linocut" },
  { url: landing_work_gallery024, category: "Mango Fashion Lookbook Campaign", span: "row_span_2", title: "Designer Linocut" },
  { url: landing_work_gallery025, category: "Mango Fashion Lookbook Campaign", span: "row_span_1", title: "Designer Linocut" },
  { url: landing_work_gallery026, category: "Mango Fashion Lookbook Campaign", span: "row_span_2", title: "Designer Linocut" },
  { url: landing_work_gallery027, category: "Mango Fashion Lookbook Campaign", span: "row_span_1", title: "Designer Linocut" },
  { url: landing_work_gallery028, category: "Mango Fashion Lookbook Campaign", span: "row_span_1", title: "Designer Linocut" },
];

function OurWork() {
  const galleryRef = useRef(null);

  useEffect(() => {
    if (typeof window === 'undefined') return;

    const initGallery = async () => {
      const [{ default: lightGallery }, { default: lgZoom }, { default: lgThumbnail }] = await Promise.all([
        import("lightgallery"),
        import("lightgallery/plugins/zoom"),
        import("lightgallery/plugins/thumbnail"),
      ]);

      await Promise.all([
        import("lightgallery/css/lightgallery.css"),
        import("lightgallery/css/lg-zoom.css"),
        import("lightgallery/css/lg-thumbnail.css"),
      ]);

      const instance = lightGallery(galleryRef.current, {
        plugins: [lgZoom, lgThumbnail],
        speed: 500,
        thumbnail: true,
        zoom: true,
      });

      return () => instance?.destroy?.();
    };

    initGallery();
  }, []);

  // Return `null` during SSR
  if (typeof window === 'undefined') return null;
  

  return (
    <div className={our_work_container} id="ourwork">
      <div className={photo_grid_container}>
        <div className={work_header}>
          <p className={work_subtitle}>OUR WORK</p>
          <h2 className={work_title}>
            Check out our gallery and discover the superior quality of our outstanding work.
          </h2>
        </div>

        <div className={photo_grid} ref={galleryRef}>
          {photos.map((photo, index) => (
            <a
              key={index}
              href={photo.url}
              className={`${photo_item} ${photo.span}`}
              data-lg-size="1000-600"
              data-sub-html={`<h4>${photo.title}</h4><p>${photo.category}</p>`}
            >
              <img
                src={photo.url}
                alt={`Photo ${index + 1}`}
                className={photo_image}
                loading="lazy"
              />
              <div className={photo_overlay}>
                <div className={overlay_content}>
                  <h1>{photo.title}</h1>
                  <span className={photo_category}>{photo.category}</span>
                </div>
              </div>
            </a>
          ))}
        </div>
      </div>
    </div>
  );
}

export default OurWork;
